@font-face {
    font-family: 'omo_font'; /* Specify the font name */
    src: url('../assets/omo_font.ttf'); /* Provide the path to your .ttf file */
}

*{
    font-family: "omo_font";
}

.love_body{
    padding-top: 10vw;
    display: block;
}

.center{
    text-align: center;
}

.half {
    flex: 1;
    height: 100%;
    /* border: 1px solid #000;  */
}

.container {
    display: flex;
    height: 100%;
    /* border: 1px solid red; */
}

.response{
    font-family: "omo_font";
    font-size: 3vw;
    color: black;
    padding-left: 10vw;
}

.choice{
    font-family: "omo_font";
    font-size: 3vw;
    color: black;
}

.left_choice{
    font-family: "omo_font";
    font-size: 2vw;
